.profile-avatar-picker {
  background-color: #000000b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.profile-avatar-picker label[for="profile_avatar"] {
  z-index: 1;
  text-align: center;
  text-align: center;
  color: #ccc;
  border: 2px dashed #ccc;
  border-radius: .3rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 85%;
  height: 75%;
  font-size: 15px;
  font-weight: bold;
  display: flex;
  position: absolute;
}

.profile-avatar-picker label[for="profile_avatar"].success {
  color: green;
}

.profile-avatar-picker label[for="profile_avatar"] input[type="file"] {
  z-index: 5;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.profile-avatar-picker label[for="profile_avatar"] input[type="file"]::file-selector-button {
  display: none;
}

.profile-avatar-picker.hidden {
  display: none;
}

.profile-avatar-picker--modal {
  background-color: #fff;
  border-radius: .3rem;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  min-width: 285px;
  min-height: 185px;
  padding: 10px 15px;
  display: flex;
  position: relative;
}

.profile-avatar-picker--modal--close {
  cursor: pointer;
  z-index: 2;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  font-size: 18px;
  display: inline-flex;
  position: absolute;
  top: -5px;
  right: -5px;
}

tr.user-profile-picture td img {
  object-fit: cover;
  max-width: 96px;
  max-height: 96px;
}

